body {
  background-color: #00FF00;
}

.head {
  font-weight: bold;
  font-size: 70px;
}

.fan-message {
  color: blue;
  font-style: oblique;
  text-align: center;
  font-size: 40px;
  margin: 0px;
}
p{
  text-align: center;
  font-size: 40px;
  margin: 0px;  
}

.grid-container {
  display: grid;
  grid-template-columns: auto auto auto;
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row: 1;
}

.main {
  grid-column: 2;
  grid-row: 1;
}

.flop-left {
  grid-column: 1;
  grid-row: 1;
}

.flop-right {
  grid-column: 3;
  grid-row: 1;
}